footer {

    p,
    ul {
        margin: 0;
        padding: 0;
    }

    text-align: center;

    background:linear-gradient(270deg, rgba(100, 223, 222, 0.04) 0%, rgba(100, 223, 222, 0.1) 47%, rgba(100, 223, 222, 0.04) 100%);

    .footer-box {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
        padding: 2% 12%;

        .footer-item {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;

            div {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;

                p {
                    font-size: 18px;
                    font-family: PingFang-SC-Medium, PingFang-SC;
                    font-weight: 500;
                    color: rgba(100, 223, 222, 1);
                    line-height: 23px;
                }

                a {
                    font-size: 12px;
                    font-family: PingFangSC-Light, PingFang SC;
                    font-weight: 300;
                    color: rgba(255, 255, 255, .7);
                    line-height: 16px;
                }
            }
        }

        .footer-logo {
            cursor: pointer;
            flex-direction: row !important;
            align-items: flex-start !important;

            img {
                width: 50px;
                height: 50px;
            }

            div {
                text-align: left;
                margin-left: 4px;

                p {
                    font-size: 22px;
                    font-family: PingFang-SC-Medium, PingFang-SC;
                    font-weight: 500;
                    color: rgba(100, 223, 222, 1);
                    line-height: 30px;
                }

                span {
                    font-size: 12px;
                    font-family: PingFangSC-Semibold, PingFang SC;
                    font-weight: 600;
                    color: rgba(100, 223, 222, 1);
                    line-height: 15px;
                    margin-top: 4px;
                }

            }
        }

        .footer-item div a:hover {
            text-shadow: 0px 2px 4px rgba(100, 223, 222, 1);
            color: rgba(255, 255, 255, 1);
        }
    }

    .explanationSegmentationImg {
        width: 100%;
        height: 12px;
    }

    .record {
        font-size: 14px;
        font-family: PingFangSC-Light, PingFang SC;
        font-weight: 300;
        color: rgba(255, 255, 255, .7);
        line-height: 20px;
        text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
        padding-bottom: 10px !important;
    }
}